.Section1-container {
    background: url('./backgroundimage.jpg') center center/cover no-repeat;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0, 0.2);
    object-fit: contain;
}

.Section1-container > h1 {
    color:#FFFF;
    font-size: 100px;
    margin-top: 100px;
}

.Section1-container > p {
    color: #FFFF; 
    font-size: 50px;
     margin-top: 90px;
}

@media screen and (max-width: 960px) {
    .Section1-container > h1 {
        font-size: 70px;
        margin-top: -150px;
    }
}

@media screen and (max-width: 768px) {
    .Section1-container > h1 {
        font-size: 50px;
        margin-top: -100px;
    }
    .Section1-container > p {
        font-size: 30px;
    }
}